import React, { Component } from 'react'
import { render } from 'react-dom'
import Gallery from 'react-photo-gallery'
import Lightbox from 'react-images'

const photos = [
  { src: require('../pictures/DSC_0620.JPG'), width: 4, height: 3 },
  { src: require('../pictures/DSC_0666.JPG'), width: 4, height: 3 },
  { src: require('../pictures/DSC_0678.JPG'), width: 4, height: 3 },
  { src: require('../pictures/DSC_0697.JPG'), width: 4, height: 3 },
  { src: require('../pictures/DSC_0760.JPG'), width: 4, height: 3 },
  { src: require('../pictures/DSC_0791.JPG'), width: 4, height: 3 },
  { src: require('../pictures/DSC_0794.JPG'), width: 4, height: 3 },
  { src: require('../pictures/DSC_0897.JPG'), width: 4, height: 3 },
  { src: require('../pictures/DSC_0898.JPG'), width: 4, height: 3 },
  { src: require('../pictures/DSC_0936.JPG'), width: 4, height: 3 },
  { src: require('../pictures/DSC_0570.JPG'), width: 4, height: 3 },
  { src: require('../pictures/DSC_0546.JPG'), width: 4, height: 3 },
  { src: require('../pictures/DSC_0532.JPG'), width: 4, height: 3 },
  { src: require('../pictures/DSC_0367.JPG'), width: 4, height: 3 },
  { src: require('../pictures/DSC_0331.JPG'), width: 4, height: 3 },
  { src: require('../pictures/DSC_0321.JPG'), width: 4, height: 3 },
  { src: require('../pictures/DSC_0300.JPG'), width: 4, height: 3 },
  { src: require('../pictures/DSC_0293.JPG'), width: 4, height: 3 },
  { src: require('../pictures/DSC_0278.JPG'), width: 4, height: 3 },
  { src: require('../pictures/DSC_0253.JPG'), width: 1, height: 1 },
  { src: require('../pictures/DSC_0198.JPG'), width: 3, height: 4 },
  { src: require('../pictures/DSC_0197.JPG'), width: 3, height: 4 },
  { src: require('../pictures/DSC_0178.JPG'), width: 3, height: 4 },
  { src: require('../pictures/DSC_0114.JPG'), width: 4, height: 3 },
  { src: require('../pictures/DSC_0088.JPG'), width: 3, height: 4 },
  { src: require('../pictures/DSC_0082.JPG'), width: 4, height: 3 },
  { src: require('../pictures/DSC_0032.JPG'), width: 4, height: 3 },
  { src: require('../pictures/DSC_0022.JPG'), width: 4, height: 3 }
]

class GalleryComponent extends Component {

  state = { currentImage : 0 }
  closeLightbox = this.closeLightbox.bind(this)
  openLightbox = this.openLightbox.bind(this)
  gotoNext = this.gotoNext.bind(this)
  gotoPrevious = this.gotoPrevious.bind(this)

  openLightbox(event, obj) {
    this.setState({
      currentImage: obj.index,
      lightboxIsOpen: true,
    })
  }
  closeLightbox() {
    this.setState({
      currentImage: 0,
      lightboxIsOpen: false,
    })
  }
  gotoPrevious() {
    this.setState({
      currentImage: this.state.currentImage - 1,
    })
  }
  gotoNext() {
    this.setState({
      currentImage: this.state.currentImage + 1,
    })
  }

  // eslint-disable-next-line
  render () {
    return (
      <div>
        <Gallery photos={photos} onClick={this.openLightbox} />
        <Lightbox images={photos}
          onClose={this.closeLightbox}
          onClickPrev={this.gotoPrevious}
          onClickNext={this.gotoNext}
          currentImage={this.state.currentImage}
          isOpen={this.state.lightboxIsOpen}
        />
      </div>
    )
  }
}
export default GalleryComponent



